<script>

import cityData from '../../utils/citydata'

export default {
  name: 'Order',
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      orderList: [],
      addressVisible: false,
      addressForm: {
        address1: [],
        detailAdd: ''
      },
      addressFormRules: {
        address1: [
          {
            required: true,
            message: '/',
            trigger: 'blur'
          }
        ],
        detailAdd: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // /
      cityData: cityData,
      cityProps: {
        // el-cascader,cascadeProps
        expandTrigger: 'hover'
      },
      progressVisible: false,
      progressInfo: []
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    /**
     * 
     */
    async getOrdersList() {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      if (res.meta.status !== 200) {
        return this.$http.error('!')
      } else {
        this.total = res.data.total
        this.orderList = res.data.goods
      }
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrdersList()
    },
    /**
     * 
     */
    showBox() {
      this.addressVisible = true
    },
    /**
     *  
     */
    addressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },
